import React from "react";
import acupuntura1 from "../img/acupuntura1.jpg";
import { Image, Card, CardGroup } from "react-bootstrap";
import moxabust1 from "../img/moxabust.jpg";
import moxabust2 from "../img/moxabust2.jpg";
import moxabust3 from "../img/moxabust3.jpg";
import moxabust4 from "../img/moxabust4.jpg";
import { Link } from "react-router-dom";

const Acupuntura = () => {
	return (
		<div class="container">
			<h2 className="abstract">Acupuntura</h2>
			<hr></hr>
			<div class="row">
				<div class="col-sm-4">
					<Image fluid src={acupuntura1} alt=""></Image>
				</div>
				<div class="col-sm-8">
					<p>
						Utilizando de agulhas para puncionar pontos específicos dos
						meridianos energéticos é possível lidar com vários tipos de
						problemas, desde dores musculares até enxaquecas e distúrbios
						emocionais.
					</p>
				</div>
			</div>
			<hr></hr>
			<h2>Técnicas extras aplicadas: </h2>
			<hr></hr>
			<div class="row">
				<div class="col-sm-4">
					<Image thumbnail className="frontimage2" src={moxabust1} alt=""></Image>
				</div>
				<div class="col-sm-8">
					<h3>Ventosaterapia</h3>
					<p>
						Uso de copos em pontos específicos para aliviar dores musculares.
					</p>
				</div>
			</div>
			<div class="row">
				<div class="col-sm-4">
					<Image thumbnail className="frontimage2" src={moxabust2} alt=""></Image>
				</div>
				<div class="col-sm-8">
					<h3>Moxabustão</h3>
					<p>
						Uso da queima da erva Artemísia tanto direta quanto indiretamente
						para aliviar inflamações e dores no geral.
					</p>
				</div>
			</div>
			<div class="row">
				<div class="col-sm-4">
					<Image thumbnail className="frontimage2" src={moxabust3} alt=""></Image>
				</div>
				<div class="col-sm-8">
					<h3>Hai-Hua</h3>
					<p>
						Técnica chinesa baseada nos meridianos onde utilizamos um aparelho
						que reage aos canais energéticos, destravando musculos e liberando
						os canais para aliviar a dor
					</p>
				</div>
			</div>
			<div class="row">
				<div class="col-sm-4">
					<Image thumbnail className="frontimage2" src={moxabust4} alt=""></Image>
				</div>
				<div class="col-sm-8">
					<h3>Auriculoterapia</h3>
					<p>
						Técnica chinesa baseada no microsistema da orelha utilizada para
						questões tanto fisicas quanto emocionais.
					</p>
				</div>
			</div>
			<hr></hr>
			<h3>Preços</h3>
			<CardGroup>
				<Card>
					<Card.Header>1 Sessão</Card.Header>
					<Card.Body>
						<Card.Title>R$ 150 por sessão</Card.Title>
						<Card.Text>
							Sessão completa de 60 minutos de Acupuntura Sistêmica, podendo
							incluir todas outras técnicas mencionadas.
						</Card.Text>
					</Card.Body>
				</Card>
				<Card>
					<Card.Header className="blue">5 Sessões</Card.Header>
					<Card.Body>
						<Card.Title>R$ 100 por sessão</Card.Title>
						<Card.Text>Pacote com 5 sessões por um bom valor.</Card.Text>
					</Card.Body>
					<Card.Footer className="blue">
						<small>R$ 500 a vista.</small>
					</Card.Footer>
				</Card>
				<Card>
					<Card.Header className="green">10 Sessões</Card.Header>
					<Card.Body>
						<Card.Title>R$ 90 por sessão</Card.Title>
						<Card.Text>Pacote com 10 sessões pelo melhor valor.</Card.Text>
					</Card.Body>
					<Card.Footer className="green">
						<small>R$ 900 a vista.</small>
					</Card.Footer>
				</Card>
			</CardGroup>
			<space2></space2>
			<p></p>
			<Link className="glow" to="/contato">
				<text className="contact">Agende sua consulta</text>
			</Link>
			<p></p>
			<space2></space2>
			<hr></hr>
		</div>
	);
};
export default Acupuntura;
