import React from "react";
import "./topbar.scss";
import { Nav, NavDropdown, Navbar } from "react-bootstrap";
import { Link } from "react-router-dom";
import Logo from "./img/logo.jpeg";

export default function topbar() {
	return (
		<Navbar sticky='top' expand="xl">
			<Navbar.Brand>
				<Link to="/">
					<img
						src={Logo}
						alt=""
						className="logo"
					/>
					<h3 className='plant' style={{display:'inline'}}>Elán</h3>
				</Link>
			</Navbar.Brand>
			<Navbar.Toggle aria-controls="basic-navbar-nav" />
			<Navbar.Collapse id="basic-navbar-nav">
				<Nav className="mr-auto">
					<Nav.Link className="linktext">
						<Link  className='text' to="/acupuntura">
							Acupuntura
						</Link>
					</Nav.Link>
					<NavDropdown
						title="Massoterapia"
						className="linktext"
						id="basic-nav-dropdown"
					>
						<NavDropdown.Item className='nocursors'>
							<Link className='text' to="/massoterapia">
								<h4>Massoterapia</h4>
							</Link>
						</NavDropdown.Item >
						<NavDropdown.Divider></NavDropdown.Divider>
						<NavDropdown.Item className='nocursors'>
							<Link  className='text' to="/massoterapia/reajustepostural">
								Reajuste Postural
							</Link>
						</NavDropdown.Item >
						<NavDropdown.Item className='nocursors'>
							<Link  className='text' to="/massoterapia/reflexologia">
								Reflexologia
							</Link>
						</NavDropdown.Item >
						<NavDropdown.Item className='nocursors'>
							<Link  className='text' to="/massoterapia/shiatsu">
								Shiatsu
							</Link>
						</NavDropdown.Item >
						<NavDropdown.Item className='nocursors'>
							<Link  className='text' to="/massoterapia/bioenergetica">
								Bioenergética
							</Link>
						</NavDropdown.Item >
						<NavDropdown.Item className='nocursors'>
							<Link  className='text' to="/massoterapia/relaxante">
								Relaxante
							</Link>
						</NavDropdown.Item >
					</NavDropdown>
					<Nav.Link className="linktext">
						<Link  className='text' to="/produtos">
							Produtos Naturais{" "}
						</Link>
					</Nav.Link>
					<Nav.Link className="linktext">
						<Link  className='text' to="/sobre">
							Sobre Nós{" "}
						</Link>
					</Nav.Link>
				</Nav>
					<Link className="glow" to="/contato">
						<text className='contact2'>Contato</text>
					</Link>
			</Navbar.Collapse>
		</Navbar>
	);
}
