import React from "react";
import acupuntura1 from "../../img/acupuntura1.jpg";
import { Image, Card, CardGroup } from "react-bootstrap";
import { Link } from "react-router-dom";

const Shiatsu = () => {
	return (
		<div class="container">
			<h2 className="plant">Shiatsu</h2>
			<hr></hr>
			<div class="row">
				<div class="col-sm-4">
					<Image fluid src={acupuntura1} alt=""></Image>
				</div>
				<div class="col-sm-8">
					<p className="left">
					Massagem feita sem óleo ou creme, é uma massagem forte, que utiliza do conhecimento de medicina chinesa sobre os meridianos energéticos para benefício do corpo. É uma massagem ótima para dores musculares e para bem estar geral.
					</p>
				</div>
			</div>
			<hr></hr>
			<h3>Preços</h3>
			<CardGroup>
				<Card>
					<Card.Header>1 Sessão</Card.Header>
					<Card.Body>
						<Card.Title>R$ 150 por sessão</Card.Title>
						<Card.Text>
							Sessão completa de 60 minutos de Massoterapia Shiatsu.
						</Card.Text>
					</Card.Body>
				</Card>
				<Card>
					<Card.Header className="blue">5 Sessões</Card.Header>
					<Card.Body>
						<Card.Title>R$ 140 por sessão</Card.Title>
						<Card.Text>Pacote com 5 sessões por um bom valor.</Card.Text>
					</Card.Body>
					<Card.Footer className="blue">
						<small>R$ 700 a vista.</small>
					</Card.Footer>
				</Card>
				<Card>
					<Card.Header className="green">10 Sessões</Card.Header>
					<Card.Body>
						<Card.Title>R$ 120 por sessão</Card.Title>
						<Card.Text>Pacote com 10 sessões pelo melhor valor.</Card.Text>
					</Card.Body>
					<Card.Footer className="green">
						<small>R$ 1200 a vista.</small>
					</Card.Footer>
				</Card>
			</CardGroup>
            <space2></space2>
            <p></p>
            <Link className="glow" to="/contato">
						<text className='contact'>Agende sua consulta</text>
					</Link>
                    <p></p>
                    <space2></space2>
                    <hr></hr>
		</div>
	);
};
export default Shiatsu;
