import React from "react";
import produto3 from "../../img/produto3.png";
import { Link } from "react-router-dom";

const Produto3 = () => {
	return (
		<div>
			<div class="row">
				<div class="col-sm-4">
					<div class="img-magnifier-container">
						<img src={produto3} className="productimage" />
					</div>
				</div>
				<div class="col-sm-8">
					<h2>Super Balm Multi-uso</h2>
					<hr></hr>
					<p>
						Corrige falhas em sobrancelhas e fortalece os cílios; hidrata os
						lábios, região dos olhos, cutículas e áreas ressecadas.
					</p>
					<hr></hr>
					<div class="row">
						<div class="col-sm-6">
							<strong>Ingredientes:</strong>
							<ul>
								<li>Manteiga de karité</li>
								<li>Manteiga de cupuaçu</li> 
								<li>Gérmen de trigo</li>
								<li>Óleo de rícino</li>
								<li>D-pantenol</li>
								<li>Óleo essencial de gerânio.</li>
							</ul>
						</div>
						<div class="col-sm-6">
							<strong>Preço</strong>
							<p>R$ 25.00 (Entrega Incluída)</p>
							<p>
								<Link className="glow" to="/contato">
									<text className="contact">Compre por mensagem</text>
								</Link>
							</p>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};
export default Produto3;
