import React from "react";
import { Carousel, Image } from "react-bootstrap";
import massage1 from "../img/massagem1.jpg";
import acupuntura1 from "../img/acupuntura1.jpg";
import massage2 from "../img/massagem2.jpg";
import acupuntura2 from "../img/acupuntura2.jpg";
import produto2 from "../img/produto2.jpg";
import { Link } from "react-router-dom";
const Home = () => {
	return (
		<div>
			<Carousel>
				<Carousel.Item>
					<Image className="carouselitem" src={acupuntura1} alt="First slide" />
					<Carousel.Caption>
						<h3>
							<text className="faintbackground">Acupuntura</text>
						</h3>
					</Carousel.Caption>
				</Carousel.Item>
				<Carousel.Item>
					<Image className="carouselitem" src={massage1} alt="" />
					<Carousel.Caption>
						<h3>
							<text className="faintbackground">Massoterapia</text>
						</h3>
					</Carousel.Caption>
				</Carousel.Item>
				<Carousel.Item>
					<Image className="carouselitem" src={produto2} alt="" />
					<Carousel.Caption>
						<text className="cursive">
							<text className="faintbackground">Produtos Naturais</text>
						</text>
					</Carousel.Caption>
				</Carousel.Item>
			</Carousel>
			<h1 className="plant">Elán.</h1>
			<div>
				Criada para te ajudar a lidar com o mundo moderno, a Elán procura
				oferecer serviços que te protegem contra estresses, ajudam com sua
				saúde, e te garantam confiança para seguir a frente com sua vida.
				<hr></hr>
				<div class="row">
					<div class="col-sm-4">
						<Link to="/massoterapia">
							<Image className="frontimage" src={massage2} alt="" />
						</Link>
					</div>
					<div class="col-sm-8">
						<h3 className="water">Massoterapia</h3>
						<p>
							A Massoterapia é perfeita para não só desestressar, mas arrumar
							sua postura, alinhar as suas energias, relaxamento completo, e
							regulação da circulação no corpo.
						</p>{" "}
						<Link className="glow" to="/massoterapia">
							<text className="bold">Saiba Mais</text>
						</Link>
					</div>
				</div>
				<hr></hr>
				<div class="row">
					<div class="col-sm-8">
						<h3 className="abstract">Acupuntura</h3>
						<p>Uma técnica chinesa milenar, a Acupuntura ajuda com diversos problemas de saúde, resolvendo dores musculares, enxaquecas, estresses, problemas energéticos, e muito mais. Além da Acunputura, oferecemos serviços de Moxabustão, Huai-Huai, Auriculoterapia, e Ventosaterapia.</p>
						<Link className="glow" to="/acupuntura">
							<text className="bold">Saiba Mais</text>
						</Link>
					</div>
					<div class="col-sm-4">
						<Link to="/acupuntura">
							<Image className="frontimage" src={acupuntura2} alt="" />
						</Link>
					</div>
				</div>
				<hr></hr>
				<div class="row">
					<div class="col-sm-4">
						<Link to="/produtos">
							<Image className="frontimage" src={produto2} alt="" />
						</Link>
					</div>
					<div class="col-sm-8">
						<h3 className="plant">Produtos Naturais</h3>
						<p>
							Aqui na Elán, além de cuidarmos de sua saúde com consultas, também
							oferecemos produtos caseiros, 100% naturais, para você se cuidar
							em qualquer lugar. Entregamos na sua mão, ou na sua casa, ou em
							alguma consulta.
						</p>
						<Link className="glow" to="/produtos">
							<text className="bold">Veja nosso catálogo</text>
						</Link>
					</div>
				</div>
				<hr></hr>
			</div>
		</div>
	);
};
export default Home;
