import React from "react";
import produto4 from "../../img/produto4.png";
import { Link } from "react-router-dom";

const Produto4 = () => {
	return (
		<div>
			<div class="row">
				<div class="col-sm-4">
					<div class="img-magnifier-container">
						<img src={produto4} className="productimage" />
					</div>
				</div>
				<div class="col-sm-8">
					<h2>Spray Facial Revigorante</h2>
					<hr></hr>
					<p>
						Spray perfeito para o verão! Com ações hidratantes, antissépticas e
						anti-bacterianas, sua composição é efetiva para desinfetar, e
						refrescante contra irritações e picadas de insetos.
					</p>
					<hr></hr>
					<div class="row">
						<div class="col-sm-6">
							<strong>Ingredientes:</strong>
							<ul>
								<li>Óleo essencial de Melaleuca (Tea Tree)</li>
								<li>Óleo essencial de Gerânio</li>
							</ul>
						</div>
						<div class="col-sm-6">
							<strong>Preço</strong>
							<p>R$ 25.00 (Entrega Incluída)</p>
							<p>
								<Link className="glow" to="/contato">
									<text className="contact">Compre por mensagem</text>
								</Link>
							</p>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};
export default Produto4;
