import React from "react";
import produto1 from "../../img/produto1.jpg";
import { Link } from "react-router-dom";
    
const Produto1 = () => {
	return (
		<div>
			<div class="row">
				<div class="col-sm-4">
					<div class="img-magnifier-container">
						<img src={produto1} className="productimage" />
					</div>
				</div>
				<div class="col-sm-8">
					<h2>Creme Revitalizante Noturno</h2>
					<hr></hr>
					<p>
						Um creme perfeito para suas noites, com efeitos positivos na
						visco-elasticidade da pele, tonificação de tecido, linhas de
						expressão, e maciez na pele. Além disso, contribui para a hidratação
						e manunteção do colágeno, e combate a Acne.
					</p>
					<hr></hr>
					<div class="row">
						<div class="col-sm-6">
							<strong>Ingredientes:</strong>
							<ul>
								<li>Ácido Hialurônico</li>
								<li>Proteína de Trigo</li>
								<li>Oléo Vegetal de Damasco</li>
								<li>Essência de Grapefruit</li>
							</ul>
						</div>
						<div class="col-sm-6">
							<strong>Preço</strong>
							<p>R$ 25.00 (Entrega Incluída)</p>
                            <p>
							<Link className="glow" to="/contato">
								<text className="contact">Compre por mensagem</text>
							</Link>
                            </p>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};
export default Produto1;
