import React from "react";
import { Image } from "react-bootstrap";
import { Link } from "react-router-dom";
import massage2 from "../img/massagem2.jpg";
import massage1 from "../img/massagem1.jpg";
import chakra from "../img/chakra.jpg";
import postura from "../img/postura.jpg";
import reflexologia from "../img/reflexologia.jpg";
import shiatsu from "../img/shiatsu.jpg";
const Massagem = () => {
	return (
		<div class="container">
			<h2 className="water">Massoterapia</h2>
			<hr></hr>
			<div class="row">
				<div class="col-sm-4">
					<Image className="frontimage" src={massage1} alt=""></Image>
				</div>
				<div class="col-sm-8">
					<p>
						Relaxe, respire, e deite na maca para resolver seus problemas de
						postura e estresse. A massoterapia entrega vários benefícios
						terapeûticos, perfeitos para a vida moderna.
					</p>
				</div>
			</div>
			<hr></hr>
			<h3>Tipos de Massagem</h3>
			<hr></hr>
			<div class="row">
				<div class="col-sm-4">
					<Link to="/massoterapia/reajustepostural">
						<Image className="frontimage" src={postura} alt=""></Image>
					</Link>
				</div>
				<div class="col-sm-2">
					<h4>Reajuste Postural</h4>
				</div>
				<div class="col-sm-6">
					<p>
						Protocolo geral de quiropraxia para ajuste de postura, é feita uma
						análise rápida sobre a postura do cliente, e depois realiza-se os
						ajustes (onde podem ocorrer, ou não, os famosos "estralos").
					</p>
					<Link className="glow" to="/massoterapia/reajustepostural">
						<text className="bold">Preços e Detalhes</text>
					</Link>
				</div>
			</div>
			<hr></hr>
			<div class="row">
				<div class="col-sm-4">
					<Link to="/massoterapia/reflexologia">
						<Image className="frontimage" src={reflexologia} alt=""></Image>
					</Link>
				</div>
				<div class="col-sm-2">
					<h4>Reflexologia</h4>
				</div>

				<div class="col-sm-6">
					<p>
						Massagem feita com um cristal apenas nos pés, tem reflexo benéfico
						imediato no corpo inteiro. Salvo alguns casos, como cálculos renais
						e gravidez, não tem contra indicação.
					</p>
					<Link className="glow" to="/massoterapia/reflexologia">
						<text className="bold">Preços e Detalhes</text>
					</Link>
				</div>
			</div>
			<hr></hr>
			<div class="row">
				<div class="col-sm-4">
					<Link to="/massoterapia/shiatsu">
						<Image className="frontimage" src={shiatsu} alt=""></Image>
					</Link>
				</div>
				<div class="col-sm-2">
					<h4>Shiatsu</h4>
				</div>

				<div class="col-sm-6">
					<p>
						Massagem feita sem óleo ou creme, é uma massagem forte, que utiliza
						do conhecimento de medicina chinesa sobre os meridianos energéticos
						para benefício do corpo. É uma massagem ótima para dores musculares
						e para bem estar geral.
					</p>
					<Link className="glow" to="/massoterapia/shiatsu">
						<text className="bold">Preços e Detalhes</text>
					</Link>
				</div>
			</div>
			<hr></hr>
			<div class="row">
				<div class="col-sm-4">
					<Link to="/massoterapia/bioenergetica">
						<Image className="frontimage" src={chakra} alt=""></Image>
					</Link>
				</div>
				<div class="col-sm-2">
					<h4>Bioenergética</h4>
				</div>

				<div class="col-sm-6">
					<p>
						Massagem feita com princípios da psicologia sobre as "couraças" do
						corpo, que são as áreas físicas que mais guardam as memórias de
						traumas, como por exemplo as costas. Feita com óleo, é necessário
						usar na seção um biquini ou shorts. Durante a massagem podem ocorrer
						diversas reações, como risadas ou choro, e liberação de algumas
						memórias guardadas.
					</p>
					<Link className="glow" to="/massoterapia/bioenergetica">
						<text className="bold">Preços e Detalhes</text>
					</Link>
				</div>
			</div>
			<hr></hr>
			<div class="row">
				<div class="col-sm-4">
					<Link to="/massoterapia/relaxante">
						<Image className="frontimage" src={massage2} alt=""></Image>
					</Link>
				</div>
				<div class="col-sm-2">
					<h4>Relaxante</h4>
				</div>

				<div class="col-sm-6">
					<p>
						Massagem feita com óleo, é necessário usar na seção um biquini, ou
						shorts. Tem como o objetivo apenas o relaxamento, é bastante
						efetiva.
					</p>
					<Link className="glow" to="/massoterapia/relaxante">
						<text className="bold">Preços e Detalhes</text>
					</Link>
				</div>
			</div>
			<hr></hr>
		</div>
	);
};
export default Massagem;
