import React from "react";
import { Image } from "react-bootstrap";
import aline from "../img/aline.jpg";
import arthur from "../img/arthur.jpg";

const Sobre = () => {
	return (
		<div class="container">
			<h2>Sobre Nós</h2>
			<hr></hr>
			<div class="row">
				<div class="col-sm-6 border-right ">
					<Image fluid src={arthur} className="profileimage"></Image>
					<h3>Arthur Queiroz</h3>
					<p>
						Realizando a Acupuntura, Ventosaterapia, Auriculoterapia, e
						utilizando as técnicas de Huai Huai e Moxabustão, Arthur é
						responsável e especialista pera parte de Acupuntura da Elán.
						Praticante de Kung-Fu, Arthur é treinado e integrado na
						cultura e medicina chinesa.
					</p>
					<hr></hr>
					<h4>Qualificações</h4>
					<ul>
						<li>2 anos de prática em Acupuntura</li>
						<li>Cursado em Acupuntura pela Holos</li>
					</ul>
					<hr></hr>
				</div>
				<div class="col-sm-6">
					<Image fluid src={aline} className="profileimage"></Image>
					<h3>Aline Queiroz</h3>
					<p>
						Realizando as Massoterapias e a produção dos cosméticos naturais, Aline é a outra metade da Elán. 
					</p>
					<hr></hr>
					<h4>Qualificações</h4>
					<ul>
						<li>1 anos de prática em Massoterapia</li>
						<li>Cursada em Massoterapia pela Holos</li>
						<li>Certificado em Aromaterapia pela Holos</li>
					</ul>
					<hr></hr>
				</div>
			</div>
		</div>
	);
};
export default Sobre;
