import React from "react";
import instagram from "../img/instagram.svg";
import whatsapp from "../img/whatsapp.svg";
import nubank from "../img/nubank.png";
import picpay from "../img/picpay.png";
import mercadopago from "../img/mercadopago.png";
import itau from "../img/itau.png";
import { Image } from "react-bootstrap";

const Contato = () => {
	return (
		<div class='container'>
			<h1>Contato</h1>
			<hr></hr>
			<div class="row">
				<div class="col-md-6">
					<img src={instagram} className="contacts" alt="" />
					<text className="bold">Instagram</text>
					<p>
						<a
							href="https://www.instagram.com/elan.cosmeticosnaturais/"
							className="glow"
						>
							@elan.cosmeticosnaturais
						</a>
					</p>
				</div>
				<div class="col-md-6">
					<img src={whatsapp} className="contacts" alt="" />
					<text className="bold">WhatsApp</text>
					<p>
					<text className="glow"> (11) 2959-1409 </text></p>
				</div>
			</div>
			<hr></hr>
			<div class="row">
				<div class="col-sm-6 border-right">
					<h3>Local de Atendimento</h3>
					<hr></hr>
					<div className="googlemap">
						<iframe
							src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d6148.798653648573!2d-46.61753224757422!3d-23.600834058063874!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x94ce5a2bc70fc6d7%3A0x51e134da35fa3ef1!2sAcademia%20C%C3%A2mara%20Shaolin%20-%20Kung%20Fu%2C%20Tai%20Chi%20e%20Boxe%20Chin%C3%AAs!5e0!3m2!1sen!2sbr!4v1596827788149!5m2!1sen!2sbr"
							width="100%"
							height="300"
							frameborder="0"
							allowfullscreen=""
							aria-hidden="false"
							tabindex="0"
						></iframe>
					</div>
					<p>A 5 minutos da estação Alto do Ipiranga (Linha verde)</p>
					<p>Camara Shaolin R. Marquês de Olinda, 174 - São Paulo, SP</p>
				</div>
				<div class="col-sm-6">
					<h3>Formas de Pagamento</h3>
					<hr></hr>
					<div className="moneycontainer">
						<div class="row">
							<div class="col-sm-6">
								<Image fluid src={picpay} className="money" alt="" />
							</div>
							<div class="col-sm-6">
								<Image fluid src={nubank} className="money" alt="" />
							</div>
						</div>
						<div class="row">
							<div class="col-sm-6">
								<Image fluid src={itau} className="money" alt="" />
							</div>
							<div class="col-sm-6">
								<Image fluid src={mercadopago} className="money" alt="" />
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};
export default Contato;
