import React from "react";
import Topbar from "./topbar";
import "./App.scss";
import { Route, Switch } from "react-router-dom";
import Home from "./pages/Home.js";
import Massagem from "./pages/Massagem.js";
import Acupuntura from "./pages/Acupuntura.js";
import Produtos from "./pages/Produtos.js";
import Sobre from "./pages/Sobre.js";
import Contato from "./pages/Contato.js";
import Shiatsu from "./pages/massoterapia/shiatsu.js";
import Relaxante from "./pages/massoterapia/relaxante.js";
import Bioenergetica from "./pages/massoterapia/bioenergetica.js";
import Reajustepostural from "./pages/massoterapia/reajustepostural.js";
import Reflexologia from "./pages/massoterapia/reflexologia.js";
import Produto1 from "./pages/produtos/produto1.js"
import Produto3 from "./pages/produtos/produto3.js"
import Produto4 from "./pages/produtos/produto4.js"

export default function App() {
	return (
		<div className="App">
			<header className="App-header">
				<Topbar></Topbar>
				<div className="main">
					<Switch>
						<Route path="/" component={Home} exact />
						<Route path="/elan/" component={Home} exact />
						<Route path="/massoterapia" component={Massagem} exact />
						<Route path="/massoterapia/shiatsu" component={Shiatsu} />
						<Route
							path="/massoterapia/reajustepostural"
							component={Reajustepostural}
						/>
						<Route
							path="/massoterapia/bioenergetica"
							component={Bioenergetica}
						/>
						<Route path="/massoterapia/relaxante" component={Relaxante} />
						<Route path="/massoterapia/reflexologia" component={Reflexologia} />
						<Route path="/acupuntura" component={Acupuntura} />
						<Route path="/produtos" component={Produtos} exact />
						<Route path="/produtos/produto1" component={Produto1} exact/>
						<Route path="/produtos/produto3" component={Produto3} exact/>
						<Route path="/produtos/produto4" component={Produto4} exact/>
						<Route path="/contato" component={Contato} />
						<Route path="/sobre" component={Sobre} />
					</Switch>
				</div>
			</header>
		</div>
	);
}
