import React from "react";
import acupuntura1 from "../../img/acupuntura1.jpg";
import { Image, Card, CardGroup } from "react-bootstrap";
import { Link } from "react-router-dom";

const Bioenergetica = () => {
	return (
		<div class="container">
			<h2 className="plant">Bioenergética</h2>
			<hr></hr>
			<div class="row">
				<div class="col-sm-4">
					<Image fluid src={acupuntura1} alt=""></Image>
				</div>
				<div class="col-sm-8">
					<p className="left">
						Massagem feita com princípios da psicologia sobre as "couraças" do
						corpo, que são as áreas físicas que mais guardam as memórias de
						traumas, como por exemplo as costas. Feita com óleo, é necessário
						usar na seção um biquini ou shorts. Durante a massagem podem ocorrer
						diversas reações, como risadas ou choro, e liberação de algumas
						memórias guardadas.
					</p>
				</div>
			</div>
			<hr></hr>
			<h3>Preços</h3>
			<CardGroup>
				<Card>
					<Card.Header>1 Sessão</Card.Header>
					<Card.Body>
						<Card.Title>R$ 120 por sessão</Card.Title>
						<Card.Text>
							Sessão completa de 60 minutos de Massoterapia Bioenergetica.
						</Card.Text>
					</Card.Body>
				</Card>
				<Card>
					<Card.Header className="blue">5 Sessões</Card.Header>
					<Card.Body>
						<Card.Title>R$ 110 por sessão</Card.Title>
						<Card.Text>Pacote com 5 sessões por um bom valor.</Card.Text>
					</Card.Body>
					<Card.Footer className="blue">
						<small>R$ 550 a vista.</small>
					</Card.Footer>
				</Card>
				<Card>
					<Card.Header className="green">10 Sessões</Card.Header>
					<Card.Body>
						<Card.Title>R$ 100 por sessão</Card.Title>
						<Card.Text>Pacote com 10 sessões pelo melhor valor.</Card.Text>
					</Card.Body>
					<Card.Footer className="green">
						<small>R$ 1000 a vista.</small>
					</Card.Footer>
				</Card>
			</CardGroup>
			<space2></space2>
			<p></p>
			<Link className="glow" to="/contato">
				<text className="contact">Agende sua consulta</text>
			</Link>
			<p></p>
			<space2></space2>
			<hr></hr>
		</div>
	);
};
export default Bioenergetica;
